/* reset */
$min-width: 320px;
$max-width: 1440px;
$base-bg: #0b0b0b;
$base-font-size: 14px;
$base-line-height: 18px;
$base-font-family: "Gilroy", sans-serif;
$custom-font-family: "Gilroy", sans-serif;
$link-color: #100da9;
$default-transition: all 0.3s linear;

/* main variables */

//text, titles
$text-color: #000;
$title-color: #000;
$title-font-family: $custom-font-family;

$font-size-h1: 36px;
$line-height-h1: 42px;
$font-size-h2: 32px;
$line-height-h2: 37px;
$font-size-h3: 28px;
$line-height-h3: 32px;
