* html .jspCorner { margin: 0 -3px 0 0; }

.jspArrow { width: 22px; height: 22px; border: 1px solid #a7acb8; top: -45px; border-radius: 50%; z-index: 100; speak: none; font-style: normal; font-weight: normal; text-transform: none; line-height: 1; -webkit-font-smoothing: antialiased; font-family: icomoon !important; font-variant: normal; color: #a7acb8; font-size: 9px; text-align: center; line-height: 22px; background: transparent; position: absolute; top: -17px; left: 0; }

.jspArrow::before { content: "\e918"; }

.jspArrowRight { left: auto; right: 0; }

.jspArrowRight::before { content: "\e917"; }

.jspHorizontalBar .jspArrow { width: 22px; height: 22px; }

.jspHorizontalBar .jspTrack, .jspHorizontalBar .jspDrag { opacity: 0; top: 50px; visibility: hidden; }

